.page_research_article {

	@media (max-width: 1200px) {
		.catalog__left {
			position: fixed;
			top: rem(74);
			left: 0;
			width: 100%;
			height: 100%;
			background-color: white;
			z-index: 150;
			// overflow-y: auto;
			flex: 0 0 100%;
			max-width: 100%;

			&-header {
				display: flex;
				border-top: rem(2) solid var(--c-dark);
				border-bottom: rem(2) solid var(--c-dark);
			}
		}
	}

	.article {
		margin-left: rem(66);
		margin-right: rem(66);
	}
	
}